.search .inp_wrap{
    height: 32px;
    border: 2px solid transparent;
}

.search .inp_wrap.focus{
    border-color: #7fa5e5;
}

.search input:focus{
    outline: none;
}

.search input{
    color: #606266;
    font-size: 14px;
    height: 100%;
    border-color: transparent;
    border-bottom-color: rgba(95,143,223,.2);
}

.search_content {
    height: auto;
    min-width: 115px;
    position: absolute;
    top: 50px;
    background-color: #ffffff;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 1000;
}

.search_content div{
    line-height: 36px;
    font-size: 14px;
    color: #606266;
    cursor: pointer;
    border-bottom: 1px solid rgba(74,89,106,.1);
}

.search_content div:last-child{
    border-bottom: none;
}

.search_content div:hover {background-color: #eff4fc; color: #7fa5e5;}