.dropbtn {
    color: white;
    border: none;
    width: 20px;
    height: 20px;
}

.dropdown{
    position: absolute;
    right: 0;
}

.dropdown-content {
    display: none;
    height: auto;
    min-width: 115px;
    position: absolute;
    top: 40px;
    background-color: #ffffff;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 1000;
}

.dropdown-content div{
    line-height: 36px;
    font-size: 14px;
    color: #606266;
    cursor: pointer;
}

.dropdown .dropdown-content div.selected,
.dropdown-content div:hover {background-color: #eff4fc; color: #7fa5e5;}