.cards div{
    height: 200px;
}

.cards .icon{
    width: 70px;
    height: 80px;
    background-color: #FAFAFA;
}

.cards h3{
    opacity: 0.5;
    font-weight: 700;
}

.cards h2,
.scard h2{
    font-size: 1.5em;
    font-weight: 600;
}

.cards .block{
    position: absolute;
    bottom: 0;
    border-bottom-right-radius: 0.375rem;
    border-bottom-left-radius: 0.375rem;
    height: 50px;
    width: 100%;
}

.listCard div span:first-child{
    max-width: 50%;
}

.listCard div{
    border-bottom: 1px solid rgba(74,89,106,.1);
}
.listCard div:last-child{
    border-bottom: none;
}