@tailwind base;
@tailwind components;
@tailwind utilities;

body, html {
  font-family: 'Nunito Sans', 'Helvetica', 'Arial', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  line-height: 1.4;
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  overflow: hidden;
  position: fixed;
  background: #f3f7fa;
  color: #4a596a;
}

body{
  overflow-y: scroll;
}
